*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 100%;
}

body {
  font-family: "Helvetica Neue", Arial, "Hiragino Sans", Meiryo, sans-serif;
  font-size: 0.9rem;
  color: #555555;
}

ul {
  list-style: none;
}

img {
  max-width: 100%;
}

a {
  color: #555555;
}

/*-------------------------------------------
Mainvisual
-------------------------------------------*/
#mainvisual img {
  width: 100%;
  height: 100vh;
  object-fit: cover;
  object-position: center top;
  margin-bottom: 80px;
}

/*-------------------------------------------
Content
-------------------------------------------*/
.content {
  text-align: center;
  margin-bottom: 80px;
  padding: 0 20px;
}
.content .title {
  margin-bottom: 20px;
}
.content .btn {
  text-decoration: none;
  display: inline-block;
  border: 1px solid #555555;
  padding: 18px 60px;
  font-size: 0.85rem;
}

/*-------------------------------------------
Images
-------------------------------------------*/
.images {
  display: flex;
  margin-bottom: 60px;
}
@media (max-width: 834px) {
  .images {
    flex-direction: column;
  }
}
.images .image {
  width: 33.3333333333%;
}
@media (max-width: 834px) {
  .images .image {
    width: 100%;
  }
}
.images .image img {
  width: 100%;
  height: 500px;
  vertical-align: bottom;
  object-fit: cover;
}

/*-------------------------------------------
Footer
-------------------------------------------*/
#footer {
  text-align: center;
  padding: 20px;
  font-size: 0.75rem;
}
#footer .sns {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}
#footer .sns li {
  margin: 0 10px;
}